body {
  margin: 0 auto;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

#wrapper {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.75rem;
}

.graph-row {
  display: flex;
  flex-direction: column;
}

.graph-container {
  min-height: 250px;
  width: 100%;
  background-color: #dbe1e2;
}

button {
  font-family: 'Poppins', sans-serif;
}

.graph-heading {
  text-align: center;
}
